import React, { useState } from 'react';
import s from './Profileinfo.module.css';
import ProfileStatusWithHooks from '../MyPosts/ProfileStatusWithHooks';
import usersPhoto from './../../../assets/images/users.jpg'
import InfoStatusForm from './infoStatusForm';
const Profileinfo = ({profile,status, updateStatus, isOwner, savePhoto,saveProfile}) => {/*/!*className={s.content}>*!/*/
let [editMode, setEditMode]=useState(false)
    if (!profile) {// условие, если у меня неt профайл то...
        return <loader1/>
    }
    const onChangeMainPhoto=(e)=>{
        if (e.target.files.length) {
            //если дляна у фацйла есть, то тогдда я беру файл и передаю его во внешний мир (который я выбрал)
            savePhoto(e.target.files[0])
        }
    }
    const onSubmit=(formData)=>{
        saveProfile(formData).then(()=>{
            setEditMode(false)// чтобы после сохранения режим редактирования закрывался
        })
        
    }
    return <div>
    <div>
        <img src='https://www.pnp.ru/upload/entities/2019/12/18/article/detailPicture/90/ac/96/c7/28dc349f219c5ff70af0b64bbae3632b.jpg'/>
    </div>
    <div className={s.descriptionBlock}>
        <img src={profile.photos.large || usersPhoto}/>
            {isOwner && <input type={'file'} onChange={onChangeMainPhoto}/>}{/*специальный инпут который поможет мне зделать выбор файлика  */}
        {/*когда будет выбран файл, то вызову событие onChange */}
            {editMode 
            ? <InfoStatusForm initialValues={profile} profile={profile} onSubmit={onSubmit} /> 
            : <InfoStatus goToEditMode={()=>{setEditMode(true)}} profile={profile} isOwner={isOwner} />}
            <ProfileStatusWithHooks status={status} updateStatus={updateStatus}/>
    </div>
</div>
}
const InfoStatus=({profile,isOwner, goToEditMode})=>{
        return <div>{isOwner && 
        <div><button onClick={goToEditMode}>Edit</button></div>}
        <div>
            <b>Full name</b>: {profile.fullName}
        </div>
        <div>
            <b>Looking for a job</b>: {profile.lookingForAJob ? 'yes' : 'no'}
        </div>
        {profile.lookingForAJob &&
            <div>
            <b>My proffessional skills</b>: {profile.lookingForAJobDescription}
            </div>}
        <div>
            <b>About me</b>: {profile.aboutMe ? 'yes' : 'no'}
        </div>
        <div>
            <b>Contacts</b>: {Object.keys(profile.contacts).map(keys => {
                //на базе каждого ключа то есть на даном имени- свойсьве хочу отрисовать коммпоненту контакт
                return <Contact key={keys} contactTitle={keys} contactValue={profile.contacts[keys]} />
            })}
        </div>

    </div>
}

export const Contact=({contactTitle, contactValue})=>{
    return <div className={s.contact}>
        <b>{contactTitle}</b>: {contactValue}{/*в метод подсовую обьект, ключи которого нужно получить */}
        {/*Oblect.keys пробежитсяпо этому обьекту (контактс) и засунет эти (фейсбук гитхаб)
        тоесть название свойств засунет в массив строк */}
    </div>
}
export default Profileinfo;