import axios from 'axios';
import React from 'react';
import { connect } from 'react-redux';
import Header from './Header';
import {setAuntificationUserData} from "../../redux/auntification-reducer";
import {getAuthUserDataThunkCreator} from '../../redux/auntification-reducer'
import { logout } from '../../redux/auntification-reducer'
class HeaderContainer extends React.Component{
    
    render() {
    return <Header {...this.props}/>//пробрасываю все пропсы которые прийдут
    }
}
const mapStateToProps=(state)=>({
    isAuth: state.auntification.isAuth,
    login: state.auntification.login //если я уже залогинен, то нужно показать логин
    //эти данные прийдут в хедер компоненту, хедер компонентейнерная компонента перекинет их через {...this.props} дальше в хедер и мой хедер из пропсов может достать эти значения и показать NavLink 

})
export default connect(mapStateToProps, {  logout})(HeaderContainer);