export const requiredField=value=>{
    if (value) {
        //если ошибки нет, то вернуть undefini=ed
        return undefined
    }
    //функция возвращаёт:
    //если ощибка есть, то нжно его вернуть сообщение ошибкой
    return 'Field is required!';
}
export const maxLengthCreator=(maxLength)=>(value)=>{
    if (value.length>maxLength) return 'Max length is ${maxLength} symboles';
    return undefined
}
