import React from 'react';
import styles from './FormsControles.module.css'
import { Field } from 'redux-form';

const FormControl = ({ input, meta:{touched,error}, child,children, ...props })=>{
    const hasError = touched && error;

    return (
        <div className={styles.formControl + ' ' + hasError ? styles.error : ''}>{/*если ошибка есть - покажи еррор, иначе пустоту */}
            <div>
                {children}
            </div>
            {hasError && <span>{error}</span>}{/*если этот спан был тронут то обведи ошибкой как пустой текст */}
        </div>
    )
}

export const Textarea=(props)=>{
    const { input, meta, child, ...restProps } = props

    return(
        <FormControl {...props}><textarea {...input} {...restProps} /></FormControl>//формконтрол, если ему нужно будет то он отобразит данные
    )
}
export const Input = (props) =>{
    const { input, meta, child, ...restProps } = props

    return (
    <FormControl {...props}><input {...input} {...restProps} /></FormControl>//формконтрол, если ему нужно будет то он отобразит данные
)
}
export const createField=(placeholder, name, validators, component,props={}, text="")=>{
    <Field placeholder={placeholder} name={name} validate={validators} component={component} {...props}/>

}