import React, { useEffect, useState } from 'react';
const ProfileStatusWithHooks =(props)=>{
    let [editMode,setEditMode]=useState(false);
    let [status, setStatus] = useState(props.status)
    useEffect(()=>{
        setStatus(props.status)
    }, [props.status])//синхронизироваться должен только когда изменится статус
    //юсеффект запускается только тогда, когдаодин раз в момент у меня вмонтирование компоненты
    //когда самый первый раз компонента вмонтировалась
    const activateEditMode=()=>{
        setEditMode(true)
    }
    const deactivateEditMode=() =>{
        //console.log(this.state.editMode)
       // this.setState({ //изменяем стейт костыльно (чтобы реакт отреагировал)
            //для этого есть метод сетСтейт который берется с React.Component
            //react склеит его с обьектом стейт (перезаписав все свойства)
         //   editMode: false
        //})
        setEditMode(false)
       props.updateStatus(status);//передаю кол-бек//возьму это велью у этого рэфа
        //console.log(this.state.editMode)

        //this.forceUpdate()//хак позволяющий обновить страницу(нежелательно исспользовать)
    }
    //let stateWithSetState=useState(true);
    //let editMode = stateWithSetState[0];//первым елементом сидит значение
    //let setEditMode=stateWithSetState[1]//вторым - функция, которыя это значение устанавливаёт
    const onStatusChange = (e) => {//при каждом напечатывании символов я меняю локальный стейт
        //я могу узнать новое значение, и это новое значение я могу засетать в качестве статуса моего локального стейта
        //на каждое нажатие я юуду менять локальный стейт
        setStatus(e.currentTarget.value);
    }
    return (
    <div>
            {!editMode &&//*если едитмод неравно тру , то я отображу эту дивку*/}

    <div>
                <span onDoubleClick={activateEditMode}>
            {props.status || 'Status not found'}</span>{/*либо покажется статус, либо нечего*/}
    </div>
    }
    
    {editMode &&//если едит мод - тогда фолс
    <div>
                <input onChange={onStatusChange} autoFocus={true} onBlur={deactivateEditMode}/*ввод текста в конце показывается*/
                    value={status}/>
    </div>}
    </div>
)}
export default ProfileStatusWithHooks;