const SEND_MESSAGE = 'SEND_MESSAGE';
//const UPDATE_MESSAGE='UPDATE_MESSAGE'
let initialState = {
    dialogs: [
        {name: 'Artur', id: 1},
        {name: 'Visit', id: 2}

    ],
    messages: [
        {id: 1, message: 'Hi'},
        {id: 2, message: 'Hello'},
        {id: 3, message: 'How are you?'}
    ],
}

const dialogsReducer = (state = initialState, action) => {
    let stateCopy = { ...state };/*скопировали поверхностно стейт*/
    stateCopy.messages = [ ...state.messages ];/*потом скопировал массив, другой массив сделали*/
    

    switch (action.type) {
        
        case SEND_MESSAGE:
        let body=action.newMessageBody;    //достаём боди(то-есть текст который там написан)
        {
            let stateCopy = { ...state, 
            messages:[...state.messages],
             messages: [...state.messages, { id: 4, message: body }]}; //слеой стороны мы закидываём все элементы из старого меседжа, а справа записываём еще один элемент(новый) 
            //stateCopy.messages.push({id: 4, message: body});
            return stateCopy;}
        default:
            return state;
    }
}
export const sendMessageCreator = (newMessageBody) => ({type: 'SEND_MESSAGE', newMessageBody});
export default dialogsReducer;