import reportWebVitals from './reportWebVitals';
import store from "./redux/redux-store";
import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import {BrowserRouter} from "react-router-dom";
import {Provider} from 'react-redux';
import AppContainer from './App';
let h1=document.createElement('h1')
h1.innerHTML='hello'
document.querySelector('body').appendChild(h1)
let rerenderEntireTree = () => /*передаем стейт через параметры*/ {
    ReactDOM.render(
        <BrowserRouter>{/*addPost={(message) => { alert('hi i am dumb')}} */}

            <Provider store={store}/*коткретно тебе доступен store*/>

                <AppContainer/>
            </Provider>
        </BrowserRouter>,
        document.getElementById('root') //вставляем по айдишнику
    );
}
/*все дочерние компоненты обромляю в мой StoreContext.Provider*/
/* это значит что App и вем другим компонентам будет доступен мой store*/
rerenderEntireTree();/*отрисовуем наше дерево единожды*/
 /*передаем во внутрь нашего субскрайба наше рирендеринтайртри*/
// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
