import * as axios from "axios";
const instance=axios.create({
    withCredentials:true,
    headers: {
        'API-KEY': '24e9d05a-897a-4ad2-9e75-2f3ca2291078'

    },
    baseURL: `https://social-network.samuraijs.com/api/1.0/`
})
export const usersAPI={
    
    getStatus(userId) {
        return instance.get('profile/status/'+userId)//вставляю юзерайди юзера которого я хочу получить
    },
    updateStatus(status) {
        return instance.put('profile/status', {status:status})//отправляю на сервер обьект у которого есть свойство статус(то-что требует документация)
    },
    savePhoto(filePhoto) {//если ремемберми не прийдёт, то пускай будет фолс(заглушка такая)
        var formData = new FormData();
        formData.append('image', filePhoto)
        return instance.put(`profile/photo`, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        })//третьим параметром настраиваю спецефические заголовки для этого запроса
    }
    
}
export const profileAPI={
    getProfile(userId) {
        return instance.get(`profile/`+userId)
    },
    saveProfile(profile) {
        return instance.put(`profile/`, profile)//отправляю на сервер обьект у которого есть свойство статус(то-что требует документация)

    }
}
export const auntificationAPI={
    me() {
        return instance.get(`auth/me`)
    },
    login(email, password, rememberMe=false, captcha) {//если ремемберми не прийдёт, то пускай будет фолс(заглушка такая)
        return instance.post(`auth/login`, {email, password, rememberMe,captcha}) //вместе с пост запросом я на сервак отправляю обьект данные
    },
    logout() {//если ремемберми не прийдёт, то пускай будет фолс(заглушка такая)
        return instance.delete(`auth/login`) 
    }
    
    
}
export const securityAPI = {
    getCaptchaUrl() {
        return instance.get(`security/get-captcha-url`)
    }}

