import axios from "axios";

const FOLLOW='FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS='SET_USERS';
const SET_CURRENT_PAGE='SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT ='SET_TOTAL_USERS_COUNT';
const TOGGLE_IS_FETCHING ='TOGGLE_IS_FETCHING';//переключатель
const TOGGLE_IS_FOLLOWING_PROGRESS ='TOGGLE_IS_FOLLOWING_PROGRESS'
let initialState={ /*инициализируем state*/
    /* у меня есть одноразовый обьект, который просто сидит, и у него есть какие-то стартовые данные*/
    users: [ ],
    pageSize: 5,
    totalUsersCount: 1000,
    currentPage: 1, //начальная страница
    isFetching: true,
    followingInProgress: [],//в этот масив буду помещать айдишку того пользователя, которого фоловлю\анфоловлю
    partSize:10
}
const usersReducer = (state=initialState, action ) /*в случае, если стейт сюда не прийдет, будешь тем-самым стейтом*/=> {
    switch (action.type) { /*выключатель*/ /*говорим: по какому ключю мы свитчем(по type)*/
       case FOLLOW: //описываю сценарий екшенов
            return{
                ...state, 
                //users:[...state.users]
                users: state.users.map(u=>{//мап пробегаёт по всем юзерам
                    if (u.id === action.userID) { //если айди того юзера равен айдишнику того юзера, которого надо зафоловить, а он сидит в юзерайди (в екшене)
                        return {...u, followed: true} //возвращаю копию юзера, и тогда нужно поменть фолловед на тру//то тогда я должен фолловид поменять на тру
                    } //если айдишка совпадаёт, то возвращаю копию, 
                    return u
                    }) //таким образом я сделал новый обьект
            } //скопировал стейт и внутренность юзерс
        case UNFOLLOW:
            return{
                ...state,
                users: state.users.map(u=>{
                    if (u.id===action.userID) {
                        return {...u, followed: false}
                    }
                    return u
                })
            }
        case SET_USERS:{//перезатираю свой масив
            return {...state, users:action.users}//склеиваём 2 массива //в екшене сидят пользователи, я их беру и устанавливаю в стейт(перезатераю весь свой массив)//users которые ко мне приходят, я перезатираю массив
        }
        case SET_CURRENT_PAGE: { // в этом случае карентпейдж меняю на карентпейдж который лежит в стейте
            return {...state, currentPage:action.currentPage}
        }
        case SET_TOTAL_USERS_COUNT: {
            return {...state, totalUsersCount:action.count} //меняю тоталюзерскаунт на екшенкаунт
        }
        case TOGGLE_IS_FETCHING: { //будет сидеть тру, или фолс который надо установить
            return {...state, isFetching:action.isFetching}
        }
        case TOGGLE_IS_FOLLOWING_PROGRESS:{
            return{...state, 
                followingInProgress: action.isFetching ? [...state.followingInProgress,action.userId]
                : [...state.followingInProgress.filter(id=>id!=action.userId)]}// здесь идут айдшки, и я пропускаю только ту айдишку, которая равна той айдишки что в екшене пришла//если у меня в екшене исфенчингфолс, а если тру то айдшку должен в масив только
        }
       
        default: /*если не найдется тип - то мы можем просто вернуть state*/
            return state;
    }  
   
}

export const follow = (userId) => ({ type: FOLLOW, userId }); //чтобы зафолловить кого-то, нужно знать userID
export const unfollow = (userId) => ({ type: UNFOLLOW, userId });//то-есть кроме типа у меня будет значение юзера, которого нужно будет зафолловить, или анфолловить 
export const setUsers = (users) => ({type: SET_USERS, users}); //то-есть откуда-то прийдут пользователи (из сервера), и я их установлю в стейт
export const setCurrentPage = (currentPage) => ({ type: SET_CURRENT_PAGE, currentPage }); //будет принимать текущую страничку и будет возвращать обьект
export const setTotalUsersCount=(totalUsersCount)=>({type: SET_TOTAL_USERS_COUNT, count: totalUsersCount})
//то-есть откуда-то прийдут пользователи (из сервера), и я их установлю в стейт
export const toggleIsFetching = (isFetching) => ({ type: TOGGLE_IS_FETCHING, isFetching })
export const toggleFollowingProgress = (isFetching, userId) => ({ type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, userId}) // отдаём в параметры юзера, чтобы знать какого юзера я сейчас протестирую
//const toggleIsFetchinProgress
export const getUsersThunkCreator = (currentPage, pageSize )=>{//санка которая внутри много асинхронной работы делаёт и внутри кучу диспатчей делаёт
    return (dispatch)=>{//криейтор - создатель санки
    dispatch(toggleIsFetching(true))
    dispatch(setCurrentPage(currentPage))
    axios.get(`https://social-network.samuraijs.com/api/1.0/users?page=${currentPage}&count=${pageSize}`,
        { withCredentials: true }).then(response => {
            dispatch(toggleIsFetching(false))
            dispatch(setUsers(response.data.items))
            //хочу что-то з компоненты юайной отправить в
        });}

}//санккриейтор - это функция, которая может что-то принимать, и которая возвращаёт санку 
export const followThunkCreator = (userId) => {//санка которая внутри много асинхронной работы делаёт и внутри кучу диспатчей делаёт
    return (dispatch) => {//криейтор - создатель санки
        dispatch(toggleFollowingProgress(true, userId))
        axios.post(`https://social-network.samuraijs.com/api/1.0/follow/${userId}`, {}, {
            withCredentials: true,
            headers: {
                'API-KEY': '24e9d05a-897a-4ad2-9e75-2f3ca2291078'

            }}).then(response => {// нужно слать запросы авторизовано
                    if (response.data.resultCode == 0) {//если ризалткод равняется 0, то операция по подписке выполнилась хорошо//сервер подтвердил что подписка произошла
                        dispatch(follow(userId))
                    }
                    dispatch(toggleFollowingProgress(false, userId))
                //хочу что-то з компоненты юайной отправить в
            });}}
export const unfollowThunkCreator = (userId) => {//санка которая внутри много асинхронной работы делаёт и внутри кучу диспатчей делаёт
    return (dispatch) => {//криейтор - создатель санки
        dispatch(toggleFollowingProgress(true, userId))
        axios.delete(`https://social-network.samuraijs.com/api/1.0/follow/${userId}`,  {
            withCredentials: true,
            headers: {
                'API-KEY': '24e9d05a-897a-4ad2-9e75-2f3ca2291078'

            }
        }).then(response => {// нужно слать запросы авторизовано
            if (response.data.resultCode == 0) {//если ризалткод равняется 0, то операция по подписке выполнилась хорошо//сервер подтвердил что подписка произошла
                dispatch(unfollow(userId))
            }
            dispatch(toggleFollowingProgress(false, userId))
            //хочу что-то з компоненты юайной отправить в
        });
    }
}
export default usersReducer; 
 