import React from 'react';
import MyPostsConteiner from './MyPosts/MyPostsConteiner'
import s from './Profile.module.css';
import MyPosts from './MyPosts/MyPosts';
import Profileinfo from './Profileinfo/Profileinfo'
import Profile from './Profile';
import { connect } from 'react-redux';
import axios from 'axios';
import {savePhoto, saveProfile} from './../../redux/profile-reducer'
import { withRouter } from 'react-router-dom';
import {getUserProfileThunkCreator} from './../../redux/profile-reducer'
import { Redirect } from 'react-router-dom';
import withAuthRedirect from './../hoc/withAuthRedirect'
import { compose } from 'redux';
import {getStatusThunkActionCreator} from "./../../redux/profile-reducer";
import {updateStatusThunkActionCreator} from "./../../redux/profile-reducer";
import { updateStatus } from "./../../redux/profile-reducer";
/*import Post from "./MyPosts/Post/Post";*/
class ProfileContainer extends React.Component {
    updateProfile() {
    let userId = this.props.match.params.userId;
    if (!userId) {//если юзер  айди не найден
        userId = this.props.authorizedUserId
        //но если юзерайди все-таки не оказалось после этого присаивания, то я покажу этот метод
        // if (!userId) {
        //   this.props.history.push('/login');
        // }
    }
    this.props.getUserProfileThunkCreator(userId)
    this.props.getStatusThunkActionCreator(userId)//юзерайди, статус которого нужно получить

    }
    componentDidMount() {
        this.updateProfile()
    }
    componentDidUpdate(prevProps) {
        if (this.props.match.params.userId != prevProps.match.params.userId){
            this.updateProfile()
        }
    }
    
    render() {
        <withAuthRedirect/>

        return(
            <Profile {...this.props} 
            isOwner={!!this.props.match.params.userId}//я являюсь овнером если юзерид вобще присутствуёт
            //!! - из псевдоистины делаёт булевоё значение
            profile={this.props.profile} 
            status={this.props.status} 
            updateStatus={this.props.updateStatus}
            savePhoto={this.props.savePhoto}
            />
        )
    }
}

let mapStateToProps=(state)=>({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    authorizedUserId:state.auntification.userId,//auth потомучто я в редакс-редьюсере так закомбайнил
    isAuth: state.auntification.isAuth//чтобы знать (авторизованы, или нет)
    
    
})

export default compose(connect(mapStateToProps, { getUserProfileThunkCreator, 
    getStatusThunkActionCreator, updateStatus, 
    savePhoto, saveProfile  }),
    withRouter
    
)(ProfileContainer)//обработай эти данные этим хоком и передам резулььтат конекту

