
import { getAuthUserDataThunkCreator } from "./auntification-reducer";

const INITIALIZED_SUCCESS = 'SET_INITIALIZED_SUCCESS';//установить пользовательские данные
let initialState = { /*инициализируем state*/
    /* у меня есть одноразовый обьект, который просто сидит, и у него есть какие-то стартовые данные*/
    initialized: false

}
const appReducer = (state = initialState, action) /*в случае, если стейт сюда не прийдет, будешь тем-самым стейтом*/ => {
    switch (action.type) { /*выключатель*/ /*говорим: по какому ключю мы свитчем(по type)*/
        case INITIALIZED_SUCCESS: //описываю сценарий екшенов
            return {
                ...state,
                initialized: false//деструктуризирую новый обьект дата(в него положу я : userId, email, login) 
                //когда пришли данные, я из исАунтификейшен делаю тру
                //таким образом я сделал новый обьект
            } //скопировал стейт и внутренность юзерс




        default: /*если не найдется тип - то мы можем просто вернуть state*/
            return state;
    }

}

//все данные которые нужно преобразовать - лежат в екшене
export const initializedSuccess = () => ({ type: INITIALIZED_SUCCESS, }); //чтобы зафолловить кого-то, нужно знать userID
export const initializeApp = () => (dispatch) => {//sanka//проиницализируй app

    let promise=dispatch(getAuthUserDataThunkCreator())//я задиспатчу это получение инициализацонных(авторизационных) данных
    //и когда эти данные будут получены, я должен задиспатчить инитиалайзевсаккес
    //когда ты залезовишся(асинхронная оперция закончена), тогда я говорю инитиалсаккес
    Promise.all([promise]).then(()=>{//олл значит что все промисы
        dispatch(getAuthUserDataThunkCreator())
    })
    dispatch(initializedSuccess())
}

export default appReducer;