import React from 'react';
import s from './Header.module.css'; /*импортируем s с обьекта*/
import { NavLink } from 'react-router-dom';
const Header = (props) => {
    return <header className={s.header}>
        <img className='header img' src="https://image.shutterstock.com/image-photo/majestic-sunset-mountains-landscape-wonderful-600w-1487897981.jpg" />
        <div className={s.loginBlock}>
            {props.isAuth ? <div>{props.login} - <button onClick={props.logout}>Log out</button></div>
            : <NavLink to={'/login'}>Login</NavLink>}
        </div>
    </header>
}
//то-есть если я уже авторизаван, то можно показать логин, в противном случае ссылку на авторизацию
export default Header;