import React from 'react';
import { connect } from 'react-redux';

import  { follow, setCurrentPage,  unfollow, toggleFollowingProgress, getUsersThunkCreator, followThunkCreator,unfollowThunkCreator } from '../../redux/users-reducer';
import Users from './User';
import Svg from './../common/svg/svg'
import { compose } from 'redux';
import { getCurrentPage, getFollowingInProgress, getIsFetching, getPageSize, getTotalUsersCount, getUsers } from '../../redux/users-selectors';
class UsersContainer extends React.Component {
    componentDidMount() {
        this.props.getUsersThunkCreator(this.props.currentPage, this.props.pageSize)
    }

    onPageChanged = (pageNumber) => {
        this.props.getUsersThunkCreator(pageNumber, this.props.pageSize)

    }
    render() {//рендер - это компонента дай мне джесикс


        return <>
            {this.props.isFetching ? <Svg /> : null /*если данные не отображаются, то я отображу крутилку (загрузку)*/}
            <Users
                totalUsersCount={this.props.totalUsersCount}
                pageSize={this.props.pageSize}
                currentPage={this.props.currentPage}
                onPageChanged={this.onPageChanged}
                users={this.props.users}
                follow={this.props.follow}
                unfollow={this.props.unfollow}/*перекидываю презентацонной компоненте нужные пропсы, чтобы она могла отрисоваться*/
                toggleFollowingProgress={this.props.toggleFollowingProgress}
                /*followingInProgress={this.props.followingInProgress}*/
                getUsersThunkCreator={this.props.getUsersThunkCreator}
                followingInProgress={this.props.followingInProgress}
                unfollowThunkCreator={this.props.unfollowThunkCreator}
                followThunkCreator={this.props.followThunkCreator}
            />
        </>
    }
}
let mapStateToProps = (state) => {
    return {
        users: getUsers(state),//добаляю ещё стейт, чтобы можно было что-то  выкавырнуть
        pageSize: getPageSize(state), //опрокидываем пейджсайз в свою компоненту
        totalUsersCount: getTotalUsersCount(state), //то-есть я привязываю часть странички к одной части стейта
        currentPage: getCurrentPage(state), //чтобы моя компонента в юзер полчила в пропсах это значение
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state),
        getUsersThunkCreator: getUsersThunkCreator(state)
        
    }
}
/*let mapDispatchToProps=(dispatch)=>{ //возвращает колбеки, функциия которая будет показывать какого пользователя нужно зафоловить
    return{
        follow: (userID) => {
            dispatch(followActionCreator(userID))
        },
        unfollow: (userID)=> {
            dispatch(unfollowActionCreator(userID))
        },
         setUsers: (users)=>{
            dispatch(setUsersActionCreator(users))
    },
        setCurrentPage: (pageNumber) => {dispatch(setCurrentPageActionCreator(pageNumber))},
        setTotalUsersCount: (totalCount)=>{
            dispatch(setUsersTotalCountActionCreator(totalCount))  //диспатчу тоталкаунт
        },
        toggleIsFetching: (isFetching) =>{ //колбек, который принимаёт исфетчинг
            dispatch(toggleIsFetchingActionCreator(isFetching))
        }
//екшен криейтер возвращает мне обьект,  
    }
}*/
export default compose(
        connect(mapStateToProps, { //конект определяёт, что к нему пришла не функция- а обьект, и делает автоматические обвертывания колбекамы значениямы которые сюда пришли
        follow,
        unfollow,
        setCurrentPage,
       
        toggleFollowingProgress,
        getUsersThunkCreator,
        unfollowThunkCreator,
        followThunkCreator


    }))(UsersContainer)