import {createStore, combineReducers, applyMiddleware} from 'redux'
import dialogsReducer from './dialogs-reducer';
import profileReducer from './profile-reducer';
import sitebarReducer from './sidebar-reducer';
import usersReducer from './users-reducer';
import auntificationReducer from './auntification-reducer';
import thunkMiddleware from 'redux-thunk'; //импортирую санк под именем санкмидлвейер
import {reducer as formReducer} from 'redux-form';
import appReducer from './app-reducer';
let reducers = combineReducers({
    profilePage: profileReducer, /*свойство равняется фнкции которую я определил*/
    dialogsPage: dialogsReducer,
    sitebarPage: sitebarReducer,
    usersPage: usersReducer,
    auntification: auntificationReducer,//отвечаёт за логику аунтификаци (хранит там данные)
    form: formReducer,
    app: appReducer//теперь моё апп получит знание про то, проинициализировано ли оно, или нет
});
/*после того как мы закомбайнили редьюсеры, я эти редьюсеры отдаю стору*/
let store = createStore(reducers, applyMiddleware(thunkMiddleware)); /*createStore создает стейт у которого есть 3 свойства*/
window.store=store; /*глобально достучаться до стора*/
//applyMiddleware - прими промежуточные слои
export default store;