import React from 'react';
import s from './MyPosts.module.css';
import Post from './Post/Post';
import { Field, reduxForm } from 'redux-form';
import  { requiredField, maxLengthCreator } from '../../../utilits/validators/validators';
import { Textarea } from '../../common/FormsControles/FormsControles';
import { Redirect } from 'react-router-dom';
const MyPosts = (props) => {
    let postsElements = props.posts.map(p => <Post key={p.id} message={p.message} likesCount={p.likesCount} dislikesCount={p.dislikesCount}/>) /*свойство постс*/

    let onAddPost = (values) => {
        props.addPost(values.newPostText)
    }

    return <div className='postsBlock'>
        <h3>My posts</h3>
        <AddPostFormRedux onSubmit={onAddPost}/>
        <div className={s.posts}>
            {postsElements}
        </div>
    </div>
}
const maxLenght10 = maxLengthCreator(330)
const AddNewPostForm=(props)=>{
    if (props.isAuth === false) return <Redirect to={'/login'} />;

    return <form onSubmit={props.handleSubmit}>
        <div><Field component={Textarea} //вместо тексереа маленькой рисую большую
         placeholder={'Post message'} validate={[requiredField, maxLenght10]} name='newPostText' /> {/*с помощью рефа мы стучимся на приму к дом-елементу*/}
            {/*привязываем конкретную ссылку*/}</div>
        <div>
            <button>Add post</button>
            {/*передаем функцию*/}
            {/*<button>Remove</button>*/}
        </div>
    </form>
}

const AddPostFormRedux=reduxForm({form: 'ProfileAddNewPostForm'})(AddNewPostForm)
export default MyPosts;
