//import { Input } from 'postcss '
import React from 'react'
import s from './Profileinfo.module.css'
import { Field, reduxForm } from 'redux-form'
import  { Input, Textarea} from '../../common/FormsControles/FormsControles'
import styles from '../../common/FormsControles/FormsControles.module.css' 

const InfoStatusForm = ({handleSubmit, profile, error  }) => {
    return <form onSubmit={handleSubmit} >

        <div><button onClick={()=>{}}>save </button></div>
        {error && <div className={styles.formSummaryError}>{error}</div>}{/* если у меня пропсеррор есть, тогда покажи мне эту дивку */}
        <div>
            <button>Login</button>
        </div>
        <div>
            <b>Full name</b>:  <Field placeholder={'Full name'} name={'fullName'} validate={[]} component={Input} />
        </div>
        <div>
            <b>Looking for a job</b>: 
            {<Field  name={'lookingForAJob'} validate={[]} component={Input} type={'checkbox'}/>}
        </div>
        {profile.lookingForAJob &&
            <div>
                <b>My proffessional skills</b>:   
            {<Field placeholder={'My proffessional skills'} name={'lookingForAJobDescription'} validate={[]} component={Textarea} />}
            </div>}
        <div>
            <b>About me</b>:              
            {<Field placeholder={'About me'} name={'aboutMe'} validate={[]} component={Textarea} />}

        </div>
        <div>
            <b>Contacts</b>: {Object.keys(profile.contacts).map(keys => {
                //на базе каждого ключа то есть на даном имени- свойсьве хочу отрисовать коммпоненту контакт
                return <div keys={keys} className={s.contact}>
                    <b>{keys}: {<Field placeholder={keys} 
                    name={'contacts.'+keys} validate={[]} component={Input} />}</b>
                </div>
            })}
        </div>

    </form> 
}
const InfoStatusFormReduxForm=reduxForm({form:'edit-profile'})(InfoStatusForm)
export default InfoStatusFormReduxForm