import React, { useState } from 'react';
import styles from './Users.module.css'
import usersPhoto from '../../assets/images/users.jpg' //мой jpg записался на usersPhoto
import {NavLink} from 'react-router-dom';

let User = (props) => {
    let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);
    let pages = []
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    }
    let partSize=10
    let partCount = Math.ceil(pagesCount / partSize);
    let [partNumber, setPartNumber] = useState(1)
    let leftPartPageSize = (partNumber - 1) * partSize + 1;
    let rightPartNumber = (partNumber * partSize)
    return <div>
        <div>
            {partNumber > 1 &&
                <button onClick={() => { setPartNumber(partNumber - 1) }}>Back</button>}
            {pages
                .filter(p => p >= leftPartPageSize && p <= rightPartNumber)
                .map((p) => {
                    return <span className={props.currentPage === p && styles.selectPage} key={p}
                        onClick={() => { props.onPageChanged(p) }}>{p}</span>
                })
            }
            {partCount > partNumber &&
                <button onClick={() => { setPartNumber(partNumber + 1) }}>Next</button>}
        </div>

        {props.users.map(u => <div key={u.id}>       <span>
            <div><NavLink to={'/profile/' + u.id}>
                <img src={u.photos.small != null ? u.photos.small : usersPhoto} className={styles.usersPhoto}/>
                    </NavLink> </div>
                    <div>
                        {
                            u.followed
                                ? <button disabled={props.followingInProgress.some(id => id === u.id)}
                                          onClick={() => { //если в пропсах будет тру фоловингинпрогресс, то кнопка будет задизейблена//если в масиве оть одна айдишка равна айдишке пользователя, то тогда дизейблд
                                              props.unfollowThunkCreator(u.id)

                                              //хочу что-то з компоненты юайной отправить в

                                          }}>Unfollow</button>
                                : <button disabled={props.followingInProgress.some(id => id === props.users.id)}
                                          onClick={() => {
                                              props.followThunkCreator(u.id)
                                          }}>Follow</button>//колбек фуекция
                        }
                    </div>
                </span>
            <span><span>
                    <div>{u.name}</div>
                    <div>{u.status}</div>
                </span>
                    <span>
                        <div>{"user.location.country"}</div>
                        <div>{"user.location.city"}</div>
                    </span>
                </span>
        </div>)}</div>

        }
        export default User