import React from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import News from './components/News/News';
import {BrowserRouter,  Route, withRouter} from 'react-router-dom';
import UsersConteiner from './components/Users/UsersConteiner';
import ProfileContainer from './components/Profile/ProfileContainer';
import HeaderContainer from './components/Header/HeaderContainer';
import Login from './components/Login/Login'
import { Component } from 'react';
import { connect } from 'react-redux';
import { initializeApp} from './redux/app-reducer'
import { compose } from 'redux';

import { withSuspese } from './components/hoc/withSuspense';
const DialogsConteiner=React.lazy(()=>import ('./components/Dialogs/DialogsConteiner'))
class App extends Component {
    componentDidMount() {
        this.props.initializeApp()
    }
    render() {
        if(this.props.initialized) {
            return <svg/>
        }
    return ( /*одна - большая компонента*/
        <BrowserRouter>
            <div className='app-wrapper'>
                <HeaderContainer/>
                <Navbar/>
                {/* <Profile />*/}
                <div className='app-wrapper-content'>
                    <Route path='/Message'
                           render={withSuspese(DialogsConteiner) }/>
                              
                    <Route path='/Profile/:userId?'//может быть параметр, может не быть
                           render={() => <ProfileContainer  />}/> {/*/*Ты роут отрисуешь компоненту профайл когда нужно*!/*/}
                    <Route path='/News' component={News}/> {/*Ты роут отрисуешь компоненту профайл когда нужно*/}
                    
                    <Route path='/users' render={() => <UsersConteiner /> }/>
                    <Route path='/login' render={() => <Login />} />

                </div>
                {/*зависимо от урл-адреса*/}
            </div>
        </BrowserRouter>);
}}//с помощью компоуса можна добавить сразу несколько хоков
const mapStateToProps=(state)=>({
    initialized:state.app.initialized
    
})


export default compose(withRouter,
    connect(mapStateToProps, { initializeApp }))(App);