import axios from "axios";
import { stopSubmit } from "redux-form";
import { profileAPI, usersAPI } from "../api/api";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE ='SET_USER_PROFILE';
const SET_STATUS ='SET_STATUS';
const SAVE_PHOTO_SUCCESS ='SAVE_PHOTO_SUCCESS'
let initialState={ /*инициализируем state*/
    /* у меня есть одноразовый обьект, который просто сидит, и у него есть какие-то стартовые данные*/
    posts: [ 
        { message: 'Hi, how are you?', likesCount: 0, dislikesCount: 0, id: 1 },
        { message: 'It`s my first post', likesCount: 17, dislikesCount: 10,id: 2 }],
        profile:null,//показываю то, что профиля пока нету (пустой обьект)
        status: ''
}
const profileReducer = (state=initialState, action) /*в случае, если стейт сюда не прийдет, будешь тем-самым стейтом*/=> {
    switch (action.type) { /*выключатель*/ /*говорим: по какому ключю мы свитчем(по type)*/
        case ADD_POST: { /*если у нас кейс будет ADD_POST*/
            let newPost = {
                id: 4,
                message: action.newPostText,
                likesCount: 0
            };
            let stateCopy = { 
                ...state,
                posts: [...state.posts,newPost],
                newPostText : ''
            } /*делаем копию обьекта*/
            /*делаем копию массива*/
             /*пушем сюда новые посты*/ /*и берем newPost, который сфрмировали на етап выше*/
            
            return stateCopy;} /*в каждом месте будет ретурн*/
            /*когда у нас ретурн по-среди функции, то у нас функия перестает работать, и после проваливаний в текст и мы можем брейк не исспользовать */ 
        
        case SET_USER_PROFILE:{
            return {...state, profile: action.profile}//делаю копию стейта и меняю профай на профайл который сидит  екшене
        }
        case SET_STATUS:{
            return {...state,
                status:action.status}//здесь прийдёт новый статус, я его засетаю в статус
        }
        case SAVE_PHOTO_SUCCESS: 
            return  {...state, profile: { ...state.profile, photos: action.photos}}
            //делаю профайл тот что біл, а фото меняю на те что сидят в екшене
        default: /*если не найдется тип - то мы можем просто вернуть state*/
            return state;
    }  
   
}
export const addPostActionCreator = (newPostText) => ({ type: 'ADD-POST',newPostText });
export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile})
export const setStatus = (status) => ({ type: SET_STATUS, status})
export const getUserProfileThunkCreator=(userId)=>(dispatch)=>{
    axios.get(`https://social-network.samuraijs.com/api/1.0/profile/` + userId).then(response => {

        dispatch(setUserProfile(response.data))

        //хочу что-то з компоненты юайной отправить в
});
}
export const savePhotoSuccess =(photos)=> ({ type: SAVE_PHOTO_SUCCESS, photos })
export const getStatusThunkActionCreator=(userId)=>async(dispatch)=>{
    let response= await usersAPI.getStatus(userId)
    // в респонсе зарезолвитс результат, который возвращаёт мне промис
        dispatch(setStatus(response.data))
    }
export const updateStatus = (status) =>async (dispatch) => {
    try {
    let response=await usersAPI.updateStatus(status)
        if (response.data.resultCode===0) {//усли резалткод равняется 0, то ощибки обрабатывать ненадо
            dispatch(setStatus(status))}
    }catch(error){debugger}}
export const savePhoto = (file) => async (dispatch) => {
    let response = await usersAPI.savePhoto(file)
    if (response.data.resultCode === 0) {//усли резалткод равняется 0, то ощибки обрабатывать ненадо
        dispatch(savePhotoSuccess(response.data.data.photos))//фото сидят...
    }
} 
export const saveProfile = (profile) => async (dispatch,getState) => {
    const userId =getState().auntification.userId
    const response = await profileAPI.saveProfile(profile)

    if (response.data.resultCode === 0) {//усли резалткод равняется 0, то ощибки обрабатывать ненадо
        dispatch(getUserProfileThunkCreator(userId))
    } else {
        dispatch(stopSubmit('edit-profile', {_error: response.data.messages[0]}))
        return Promise.reject(response.data.messages[0])//возвращают промис который реджектится с ошибкой
    }
}

export default profileReducer; 