import axios from "axios";
import { stopSubmit } from "redux-form";
import { auntificationAPI, securityAPI } from "../api/api";

const SET_USER_DATA ='SET_USER_DATA';//установить пользовательские данные
const SET_URL_DATA ='SET_URL_DATA';
const GET_CAPTCHA_URL_SUCCESS ='samurai-network/auth/GET_CAPTCHA_URL_SUCCESS'
let initialState={ /*инициализируем state*/
    /* у меня есть одноразовый обьект, который просто сидит, и у него есть какие-то стартовые данные*/
    userId:null,
    email: null,
    login: null,
    isAuth: false, //значение которое будет говорить(не залогинен)
    captchaUrl:null
}
const auntificationReducer = (state=initialState, action ) /*в случае, если стейт сюда не прийдет, будешь тем-самым стейтом*/=> {
    switch (action.type) { /*выключатель*/ /*говорим: по какому ключю мы свитчем(по type)*/
        case SET_USER_DATA: //описываю сценарий екшенов
        case GET_CAPTCHA_URL_SUCCESS:
            return{
                ...state,
                ...action.payload,//деструктуризирую новый обьект дата(в него положу я : userId, email, login) 
                 //когда пришли данные, я из исАунтификейшен делаю тру
                 //таким образом я сделал новый обьект
                 isAuth:true
            } //скопировал стейт и внутренность юзерс
           
        
        
       
        default: /*если не найдется тип - то мы можем просто вернуть state*/
            return state;
    }  
   
}
//все данные которые нужно преобразовать - лежат в екшене
export const setAuntificationUserData = (userId, email, login, isAuth,url) => ({ type: SET_USER_DATA, payload: { userId, email, login, isAuth,url} }); //чтобы зафолловить кого-то, нужно знать userID
export const getCaptchaUrlSuccess = (captchaUrl) =>
    ({ type: GET_CAPTCHA_URL_SUCCESS, payload: { captchaUrl} });

export const getAuthUserDataThunkCreator = ()=>async(dispatch)=>{
    //me- возвращат мне промис, я на него зенюсь
    let response = await auntificationAPI.me( /*чтобы авторизоваться*/)///делаю гет запрос на сервер//делаю гет запросы и мне вернуться юзеры //когда будет ответ(ответ прийдёт в response)выполним логику
        if (response.data.resultCode === 0) {//если в респонс дата резалткод равен 0, то мы залогинены и должны задипатчить вот-эти авторизационные данные
            let { id, login, email } = response.data.data; //делаю деструктуризацию,и передаю дальше параметры
            dispatch(setAuntificationUserData(id, email, login, true))//я их возьму из респонс дата ...
        }//тру- чтобы узтать что я залогинелся//и этот же диспатч должен бкуду задиспатчть когда я вылоганиюсь
    }
    
export const login = (email,password, rememberMe, captcha) => async(dispatch) => {
    
    //система получит общую ошибку на всю форму
    //стопсабмичу логин, а ворым обьектом указываю проблемные свойства, (например емейл, который вызвал ошибку)
    
    let response=await auntificationAPI.login(email, password, rememberMe,captcha)///делаю гет запрос на сервер//делаю гет запросы и мне вернуться юзеры //когда будет ответ(ответ прийдёт в response)выполним логику
        if (response.data.resultCode === 0) {//если в респонс дата резалткод равен 0, то мы залогинены и должны задипатчить вот-эти авторизационные данные
                dispatch(getAuthUserDataThunkCreator())
        }else  {
            if (response.data.resultCode===10) {
                dispatch(getCaptchaUrl())
            }
            let message=response.data.messages.length > 0 ? response.data.messages[0] : 'Some error'
            //если длина меседжа меньше нуля, то я покажу первое сообщение. Иначе ошибка(если массив с серака пришедший будет пустой)
            dispatch(stopSubmit('Login', {_error: message }))

        }
    }
export const getCaptchaUrl = () => async (dispatch) => {
    const response = await securityAPI.getCaptchaUrl()
    const captchaUrl = response.data.url //этот урл может пойти в юай если он станет стейтом
    dispatch(getCaptchaUrlSuccess(captchaUrl))



}
export const logout = () => async(dispatch) => {
    let response= await auntificationAPI.logout()///делаю гет запрос на сервер//делаю гет запросы и мне вернуться юзеры //когда будет ответ(ответ прийдёт в response)выполним логику
        if (response.data.resultCode === 0) {//если в респонс дата резалткод равен 0, то мы залогинены и должны задипатчить вот-эти авторизационные данные
            dispatch(setAuntificationUserData(null, null, null, false))// все зануляю(зачищаю)
        }
    }

export default auntificationReducer; 
 