import React from 'react';
import { connect } from 'react-redux';
import { Field, reduxForm } from 'redux-form';
import { requiredField } from '../../utilits/validators/validators';
import { Input } from '../common/FormsControles/FormsControles';
import { login } from '../../redux/auntification-reducer';
import { Redirect } from 'react-router';
import styles from './../common/FormsControles/FormsControles.module.css';
const LoginForm = ({ handleSubmit, error, captchaUrl}) => {
   return (
      <form onSubmit={handleSubmit}/*не перезагружай формочку*/>
         <div>
            
            <Field placeholder={'Email'} name={'email'} validate={[requiredField]} component={Input} />
         </div>
         <div>
            <Field placeholder={'Password'} name={'password'}  validate={[requiredField]} component={Input} />
         </div>
         <div>
            <Field component={Input} name={'rememberMe'} type={'checkbox'} /> remember me
         </div>
         {captchaUrl && <img src={captchaUrl} />}
         {captchaUrl && < Field component={Input} name={'captcha'} validate={[requiredField]} type={'checkbox'} />
}
         {error && <div className={styles.formSummaryError}>{error}</div>}{/* если у меня пропсеррор есть, тогда покажи мне эту дивку */}
         <div>
            <button>Login</button>
         </div>
         
      </form>
)}
const LoginReduxForm=reduxForm({form:'Login'})(LoginForm)//в кавычках форма, которую я стопсабмичу
const Login=(props)=>{
   const onSubmit=(formData)=>{
      //сюда прийдут все значения изформы
      props.login(formData.email, formData.password, formData.rememberMe,formData.captcha );
   }
   if (props.isAuth) {//если в пропсах прийдёт исаус(уже залогинен), то верну redirect
      return <Redirect to= {'/users'}/>
   }
   return  <div><h1>Login</h1>
      <LoginReduxForm onSubmit={onSubmit} captchaUrl={props.captchaUrl}/>
   </div>
}

const mapStateToProps=(state)=>({
   captchaUrl: state.auntification.captchaUrl,
   isAuth:state.auntification.isAuth
})
export default connect(mapStateToProps, {login})(Login)